import Vue from 'vue'
import Helper from '@/helper'
    import VueLodash from 'vue-lodash'
    Vue.use(VueLodash, {
        name: 'lodash'
    })

const stickyHeader = {

    bind: function (el, binding) {
        if(binding.value !== undefined && !binding.value){
            return;
        }
        let windowHeight = Helper.getScrollOffset();
        let scrollOffset;
        let thead = el.querySelector('thead');
//        let theadClone = Vue.lodash.clone(thead);
//        console.log(theadClone);
        let ths = thead.getElementsByTagName('th');
        let table = el.tagName.toLowerCase() == 'table'
                    ? el
                    : el.querySelector('table')
        
//        TODO 'resize scroll wheel' listener
        window.addEventListener('scroll', updateHeaderPosition);
         
        function updateHeaderPosition() {
                // if table is hidden we can not calculate correct position
                if (table.offsetParent === null) {
                    return;
                }

                let tableTopPosition = table.getBoundingClientRect().top;
                let offsetValue;
            
                if (tableTopPosition <= 0) {
                    scrollOffset = Helper.getScrollOffset();
                    offsetValue = (windowHeight + scrollOffset).toFixed();
                    changeHeaderOffset(offsetValue);
                } else {
                    changeHeaderOffset(0);
                }
        }
        
        function changeHeaderOffset(value) {
            for (let i = 0; i < ths.length; i++) {
                ths[i].style.transform = `translateY(${value}px)`;
            }
        }
        
//            // Clean up
//            $on('$destroy', function () {
//                el.off('resize scroll wheel', updateHeaderPosition);
//            });
    }
}

export default stickyHeader;
